import jsonpUtil from '../util/jsonpUtil'

const mce = "https://mce.mogucdn.com/jsonp/";
const list = "https://list.mogujie.com/search";
const right = "https://mce.mogujie.com/jsonp/";
const hot = "https://list.mogujie.com/module/mget";
const review = "https://rate.mogujie.com/h5/rate/jsonp/";

export default {
    loadDatasByPids(params){
        let url = mce+"multiget/3";
        return jsonpUtil.request(url,params);
    },
    loadDatasByList(params){
        return jsonpUtil.request(list,params);
    },
    loadDatasByRight(params){
        let righturl = right+"makeup/3";
        return jsonpUtil.request(righturl,params);
    },
    loadDatasByHot(params){
        return jsonpUtil.request(hot,params);
    },
    loadDatasByReview(params){
        let reviewurl = review+"x6.rate.ratelist/v1";
        return jsonpUtil.request(reviewurl,params);
    }
}


