<template>
    <div class="bebefit">
        <div v-for="(v,i) of bebefit" :key="'bebefit'+i" :class="'bebefit'+(i+1)">
            <div class="bebefit-desc">
                <div class="bebefit-title">{{ v.title }}</div>
                <div class="bebefit-viceTitle">{{ v.viceTitle }}</div>
            </div>
            <div class="bebefit-img">
                <img :src="v.image">
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        bebefit:null
    }
}
</script>

<style lang="less">
    .bebefit{
        width: 100%;
        margin-top: 20px;/*px*/
        color: #AEB9BE;
        .bebefit1{
            width: 50%;
            height: 300px;/*px*/
            border: 1px solid #ccc;
            border-left: 0px solid #ccc;
            float: left;
            box-sizing: border-box;
            .bebefit-desc{
                width: 100%;
                text-align: center;
                margin-top: 20px;/*px*/
                .bebefit-viceTitle{
                    color: #F67592;
                    margin-top: 10px;/*px*/
                }
            }
            .bebefit-img{
                width: 100%;
                height: auto;
                text-align: center;
                img{
                    width: 50%;
                    height: auto;
                }
            }
        }
        .bebefit2,.bebefit3{
            width: 50%;
            height: 150px;/*px*/
            border-top: 1px solid #ccc;
            box-sizing: border-box;
            float: left;
            display: flex;
            justify-content: space-around;
            .bebefit-desc{
                width: 60%;
                margin-left: 20px; /*px*/
                margin-top: 20px;/*px*/
                .bebefit-viceTitle{
                    color: #F67592;
                    margin-top: 10px;/*px*/
                }
            }
            .bebefit-img{
                width: 40%;
                height: auto;
                text-align: center;
                img{
                    width: 100%;
                    height: auto;
                }
            }
        }
        .bebefit3{
            border-bottom: 1px solid #ccc;
        }
        .bebefit4,.bebefit5,.bebefit6{
            width: 100%/3;
            height: 200px;/*px*/
            border-right: 1px solid #ccc;
            border-bottom: 1px solid #ccc;
            box-sizing: border-box;
            float: left;
            .bebefit-desc{
                width: 100%;
                text-align: center;
                margin-top: 20px;/*px*/
                .bebefit-viceTitle{
                    color: #F67592;
                    margin-top: 10px;/*px*/
                }
            }
            .bebefit-img{
                width: 100%;
                height: auto;
                text-align: center;
                img{
                    width: 40%;
                    height: auto;
                }
            }
        }
    }  
</style>





