<template>
    <div class="hotContianer">
        <div :style="{color:v.color}" v-for="(v,i) of searchHot" :key="'searchHot'+i" @click="searchChange(v)">{{ v.frontword }}</div>
    </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
    props:{
        searchHot:null
    },
    methods: {
        ...mapMutations(['setSearchHistory']),
        searchChange(v){
            this.$router.push('/search?'+"&title="+v.frontword);
            if(!localStorage.word){
                localStorage.word="";
            }
            localStorage.word+=v.frontword+",";
            let arr=localStorage.word.replace(/,$/,"").split(",");
            this.setSearchHistory(arr);
        }
    }
}
</script>

<style lang="less">
    .hotContianer{
        width: 90%;
        font-size: 25px;/*px*/
        margin: 0 auto;
        div{
            width: 100px;/*px*/
            height: 50px;/*px*/
            line-height: 50px;/*px*/
            text-align: center;
            border:1px solid #ccc;
            display: inline-block;
            margin-left: 20px;/*px*/
            margin-bottom: 20px;/*px*/
        }
    }
</style>




