<template>
    <div class="AllSearchType">
        <div v-for="(v,i) of searchType" :key="'searchType'+i" class="searchType" @click="goToDetail(v)">
            <div class="searchTypeImg"><img :src="v.img"></div>
            <div class="searchTypeTitle">{{ v.title }}</div>
            <div class="searchTypeDesc">
                <span class="searchTypePrice">¥{{ v.price }}</span>
                <span class="searchTypeCfav">{{ v.cfav }}<i class="iconfont icon-shoucang"></i></span>
            </div>
        </div>
        <div v-for="(v,i) of (2-(searchType.length%2==0?2:searchType.length%2))" :key="'emptySearchType'+i" class="emptySearchType"></div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
    props:{
        searchType:null
    },
    computed:{
        ...mapState(['home'])
    },
    methods:{
        ...mapMutations(['setChangeData']),
        goToDetail(v){
            this.setChangeData(v);
            this.$router.push('/detail');
        }
    }
}
</script>

<style lang="less">
    .AllSearchType{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-top: 20px;/*px*/
        .searchType{
            width: 50%;
            height: 650px;/*px*/
            .searchTypeImg{
                width: 100%;
                height: 500px;/*px*/
                text-align: center;
                border-radius: 40px 40px 0px 0px;/*px*/
                img{
                    width: 90%;
                    height: 500px;/*px*/
                }
            }
            .searchTypeTitle{
                width:80%;
                color: #AEB9BE;
                text-align: center;
                margin: 10px auto;
                overflow: hidden;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
            }
            .searchTypeDesc{
                width: 80%;
                margin: 10px auto;
                display: flex;
                justify-content: space-between;
                .searchTypePrice{
                    font-size: 30px;/*px*/
                    font-weight: bold;
                }
                .searchTypeCfav{
                    margin-right: 0px;
                    color: #AEB9BE;
                    .icon-shoucang{
                        margin-left: 10px;/*px*/
                    }
                }
            }
        }
        .emptySearchType{
            width: 50%;
            height: 650px;/*px*/
        }
    }
</style>



