<template>
    <div class="searchShop">
        <div class="searchShopNav">
            <div class="iconfont icon-xiangzuojiantou" @click="goBack()"></div>
            <div class="shopContent"><input type="text" v-model="msg"></div>
            <div class="shopWord" @click="searchChange()">搜索</div>
        </div>
        <div class="searchShopBody">
            <div class="searchShopTitle">
                <span class="iconfont icon-sousuo"><span>历史搜索</span></span>
                <span class="iconfont icon-lajixiang" @click="clearChange()"></span>
            </div>
            <div class="searchHistory">
                <div v-if="home.searchHistory[0]==''">暂无历史搜索</div>
                <div v-if="!home.searchHistory[0]==''" v-for="(v,i) of home.searchHistory" :key="'searchHistory'+i" class="historyContent">
                    {{v}}
                </div>
            </div>
        </div>
        <div class="searchHot">
            <div class="iconfont icon-shoucang"><span>热门搜索</span></div>
            <SearchHot :searchHot="home.searchHot"></SearchHot>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations} from 'vuex';

import SearchHot from './search/SearchHot';

export default {
    data(){
        return{
            msg:""
        } 
    },
    components: {
        SearchHot
    },
    computed: {
        ...mapState(['home'])
    },
    methods: {
        ...mapMutations(['setSearchHistory']),
        ...mapActions(['loadSearchHot']),
        searchChange(){
            this.$router.push('/search?'+"&title="+this.msg);
            this.loadHistory(this.msg);
        },
        loadHistory(m){
            if(!localStorage.word){
                localStorage.word="";
            }
            if(m){
                localStorage.word+=this.msg+",";
            }
            let arr=localStorage.word.replace(/,$/,"").split(",");
            this.setSearchHistory(arr);
        },
        clearChange(){
            localStorage.clear();
            this.setSearchHistory([]);
        },
        goBack(){
            this.$router.go(-1);
        }
    },
    mounted() {
        this.loadSearchHot();
        this.loadHistory();
    }
}
</script>

<style lang="less">
    .searchShop{
        width: 100%;
        .searchShopNav{
            width: 100%;
            height: 100px;/*px*/
            border-bottom: 1px solid #ccc;
            box-sizing: border-box;
            color: #aaa;
            display: flex;
            .iconfont{
                width: 10%;
                height: 100px;/*px*/
                line-height: 100px;/*px*/
                font-size: 50px;/*px*/
                text-align: center;
            }
            .shopContent{
                width: 80%;
                height: 60px;/*px*/
                margin-top: 20px;/*px*/
                input{
                    width: 90%;
                    height: 50px;/*px*/
                    border: 1px solid #ccc;
                    margin-top: 5px;/*px*/
                    border-radius: 10px;/*px*/
                    margin-left: 30px;/*px*/
                    font-size: 30px;/*px*/
                    line-height: 50px;/*px*/
                    outline: none;
                    &:focus{
                        border: 1px solid #F67592;
                    }
                }
            }
            .shopWord{
                width: 10%;
                height: 60px;/*px*/
                line-height: 60px;/*px*/
                margin-top: 20px;/*px*/
                font-size: 30px;/*px*/
            }
        }
        .searchShopBody{
            width: 100%;
            height: 180px;/*px*/
            border-bottom: 1px solid #ccc;
            .searchShopTitle{
                width: 100%;
                height: 100px;/*px*/
                line-height: 100px;/*px*/
                display: flex;
                justify-content: space-between;
                .iconfont{
                    font-size: 30px;/*px*/
                    color: rgb(97, 95, 95);
                }
                .icon-sousuo{
                    margin-left: 20px;/*px*/
                    span{
                        font-size: 25px;/*px*/
                    }
                }
                .icon-lajixiang{
                    margin-right: 40px;/*px*/
                }
            }
            .searchHistory{
                width: 90%;
                height: 80px;/*px*/
                font-size: 25px;/*px*/
                color: rgb(97, 95, 95);
                margin: 0 auto;
                div{
                    margin-left: 10px;/*px*/
                }
                .historyContent{
                    width: 100px;/*px*/
                    height: 50px;/*px*/
                    line-height: 50px;/*px*/
                    text-align: center;
                    border:1px solid #ccc;
                    display: inline-block;
                }
            }
        }
        .searchHot{
            width: 100%;
            .icon-shoucang{
                width: 100%;
                height: 100px;/*px*/
                line-height: 100px;/*px*/
                font-size: 30px;/*px*/
                color: rgb(97, 95, 95);
                margin-left: 20px;/*px*/
                span{
                    font-size: 25px;/*px*/
                }
            }
        }
    }
</style>



