import Vue from 'vue'
import App from './App'

import store from './store'

import router from './router'
import global from './global'

import 'lib-flexible';

Vue.config.productionTip = false

Vue.use(global);

new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
});



