import api from '../../server/api';

const state = {
    warpImgs:[],
    types:[],
    bebefit:[],
    hotType:[],
    likes:[],
    leftClassify:[],
    rightClassify:[],
    search:[],
    rightType:[],
    searchList:[],
    searchType:[],
    searchHot:[],
    searchHistory:[],
    changeData:{},
    review:[]
}

const getters = {

}

const actions = {
    loadHomeData({ commit }){
        api.loadDatasByPids({
            appPlat:"m",
            pids: "51822,106930,51833,51836,41789"
        }).then(({ data })=>{
            commit("setWarpImgs",data[51822].list);
            commit("setTypes",data[106930].list);
            commit("setBebefit",data[51833].list);
            commit("setHotType",data[51836].list);
            commit("setLeftClassify",data[41789].list);
        })
    },
    loadLikes({ commit }){
        api.loadDatasByList({
            pid: "9750",
            sort: "pop",
            cKey: 46
        }).then(({ result })=>{
            commit("setLikes",result.wall.docs);
        })
    },
    loadClassify({ commit },pid="41888"){
        api.loadDatasByRight({
            appPlat:"m",
            pid:pid
        }).then(({ data })=>{
            // console.log(data);
            commit("setRightClassify",data.categoryNavigation.list);
        })
    },
    loadSearch({ commit },params={sort:"pop",fcid:"10062603"}){
        // console.log(sort);
        api.loadDatasByList({
            fcid: params.fcid,
            page: 1,
            sort: params.sort,
            cKey: "h5-cube"
        }).then(({ result })=>{
            // console.log(result);
            commit("setSearch",result.sortFilter);
            commit("setRightType",result.wall.docs);
        })
    },
    loadSearchList({ commit }){
        api.loadDatasByList({
            // fcid:fcid,
            // title:title,
            // cKey:16,
            // sort:"pop",
            // page:1
        }).then(({ result })=>{
            // console.log(data);
            commit("setSearchList",result.filter.list);
        })
    },
    loadSearchType({ commit },params={fcid:"50243",title:"时尚套装",sort:"pop"}){
        api.loadDatasByList({
            fcid:params.fcid,
            title:params.title,
            cKey:16,
            sort:params.sort?params.sort:"pop",
            page:1
        }).then(({ result })=>{
            // console.log(data);
            commit("setSearchType",result.wall.docs);
        })
    },
    loadSearchHot({ commit }){
        api.loadDatasByHot({
            platfrom:"H5",
            code:"hotWord,sketch"
        }).then(({ data })=>{
            // console.log(data);
            commit("setSearchHot",data.hotWord.data);
        })
    },
    loadSearchKey({ commit },params){
        api.loadDatasByList({
            q:params.title,
            cKey:46,
            page:1,
            sort:"pop|sell|new"
        }).then(({ result })=>{
            commit("setSearchType",result.wall.docs);
        })
    },
    loadReview({ commit },pid){
        // console.log(pid);
        api.loadDatasByReview({
            type:1,
            itemid:pid,
            appPlat:"m"
        }).then(({ data })=>{
            // console.log(data);
            commit("setReview",data.list);
        })
    }
}

const mutations = {
    setWarpImgs(state,data){
        state.warpImgs = data;
    },
    setTypes(state,data){
        state.types = data;
    },
    setBebefit(state,data){
        state.bebefit = data;
    },
    setHotType(state,data){
        state.hotType = data;
    },
    setLikes(state,data){
        state.likes = data;
    },
    setLeftClassify(state,data){
        state.leftClassify = data;
    },
    setRightClassify(state,data){
        state.rightClassify = data;
    },
    setSearch(state,data){
        state.search = data;
    },
    setRightType(state,data){
        state.rightType = data;
    },
    setSearchList(state,data){
        state.searchList = data;
    },
    setSearchType(state,data){
        state.searchType = data;
    },
    setSearchHot(state,data){
        state.searchHot = data;
    },
    setSearchHistory(state,data){
        state.searchHistory = data;
    },
    setChangeData(state,data){
        state.changeData = data;
    },
    setReview(state,data){
        state.review = data;
    }
}

export default {
    state,
    mutations,
    actions,
    getters
}


