<template>
    <div class="warp">
        <swiper :options="swiperOption">
            <swiper-slide v-for="(v,i) of warpImgs" :key="'warp'+i">
                <img :src="v.image_800">
            </swiper-slide>
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
    </div>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper';

export default {
    components: {
        swiper,
        swiperSlide,
        pagination:'.swiper-pagination'
    },
    data() {
        return {
            swiperOption: {
                autoplay:2000,
                loop:true,
                pagination:'.swiper-pagination',
                paginationClickable:true
            }
        };
    },
    props:{
        warpImgs:null
    }
};
</script>
<style lang="less">
    .warp{
        width: 100%;
        height: auto;
        img{
            width: 100%;
            height: auto;
        }
        .swiper-pagination{
            background-color: rgba(51, 51, 51, 0.4);
        }
    }
</style>



