<template>
    <div class="rightClassify">
        <div v-for="(v,i) of rightClassify" :key="'rightClassify'+i" class="bigRight" @click="goToSearch(v)">
            <div class="rightImg"><img :src="v.image"></div>
            <div class="rightTitle">{{ v.title }}</div>
        </div>
        <div v-for="(v,i) of (3-(rightClassify.length%3==0?3:rightClassify.length%3))" :key="'emptyRight'+i" class="emptyRight"></div>
    </div>
</template>

<script>
export default {
    props:{
        rightClassify:null
    },
    methods:{
        goToSearch(v){
            let str=v.link.match(/fcid.[0-9]*/)[0];

            // console.log(str);
        
            this.$router.push('/search?'+str+"&title="+v.title);
        }
    }
}
</script>

<style lang="less">
    .rightClassify{
        width: 90%;
        border-bottom: 1px solid #ccc;
        margin: 0 auto;
        padding-bottom: 50px;/*px*/
        display: flex;
        flex-wrap: wrap;
        .bigRight{
            width: 100%/3;
            height: 220px;/*px*/
            text-align: center;
            .rightImg{
                width:100%;
                height:160px;/*px*/
                margin-top: 20px;/*px*/
                img{
                    width: 70%;
                }
            }
            .rightTitle{
                font-size: 25px;/*px*/
            }
        }
        .emptyRight{
            width: 100%/3;
            height: 200px;
        }
    }
</style>



