<template>
    <div class="all-hotType">
        <div v-for="(v,i) of hotType" :key="'hotType'+i" class="hotType">
            <div class="hotType-img"><img :src="v.image"></div>
            <div class="hotType-title">{{ v.title }}</div>
        </div>
    </div>    
</template>

<script>
export default {
    props:{
        hotType:null
    }
}
</script>

<style lang="less">
    .all-hotType{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-top: 20px;/*px*/
        .hotType{
            width:25%;
            text-align: center;
            .hotType-img{
                width: 100%;
                height: auto;
                img{
                    width: 80%;
                    height: auto;
                }
            }
            .hotType-title{
                color: #AEB9BE;
            }
        }
    }
</style>





