<template>
    <div class="litter-nav">
        <div v-for="(v,i) of types" :key="'littleNav'+i" class="nav-container">
            <div class="nav-img"><img :src="v.image"></div>
            <div class="nav-title">{{ v.title }}</div>
        </div>
    </div>
</template>

<script>
    export default {
        props:{
            types:null
        }
    }
</script>

<style lang="less">
    .litter-nav{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        .nav-container{
            width: 20%;
            height: 100%;
            .nav-img{
                width: 80%;
                height: auto;
                margin: 20px auto;
                img{
                    width: 100%;
                    height: auto;
                }
            }
            .nav-title{
                text-align: center;
                color: #AEB9BE;
            }
        }   
    }
</style>





