<template>
    <div class="searchContainer">    
        <div class="searchNav">
            <div class="iconfont icon-xiangzuojiantou" @click="goBack()"></div>
            <router-link to="/nav" tag="button" class="searchBar">
                <div class="iconfont icon-sousuo"></div>
            </router-link>
            <div class="iconfont icon-gouwuche"></div>
        </div>
        <div class="allList">
            <div :class="v.sort | isClick(key)" v-for="(v,i) of home.searchList" :key="'searchList'+i" class="searchList" @click="typeChange(v)">{{ v.title }}</div>
        </div>
        <div class="searchTypeContainer">
            <div class="wrapper" ref="searchType">
                <div class="content">
                    <SearchType :searchType="home.searchType"></SearchType>
                </div>
            </div>
        </div>    
    </div>
</template>

<script>
import BScroll from "better-scroll";
import { mapState, mapActions} from 'vuex';

import SearchType from './list/SearchType';

export default {
    data(){
        return {
            key: "pop"
        }
    },
    components:{
        SearchType
    },
    filters:{
        isClick(value,key){
            return value==key?"listSearch":"";
        }
    },
    computed: {
        ...mapState(['home'])
    },
    methods: {
        initScroll() {
            this.$nextTick(() => {
                this.scroll = new BScroll(this.$refs.searchType, {
                    click:true
                });
            });
        },
        ...mapActions(['loadSearchList','loadSearchType','loadSearchKey']),
        typeChange(p){
            // console.log(p);
            this.key=p.sort;
            let params=this.$route.query;
            params.sort=p.sort;
            this.loadSearchType(params);
        },
        goBack(){
            this.$router.go(-1);
        }
    },
    mounted(){
        // console.log(this.$route.query);
        this.initScroll();
        this.loadSearchList();
        // this.loadSearchType(this.$route.query);
        if(this.$route.query.fcid){
            this.loadSearchType(this.$route.query);
        }else{
            this.loadSearchKey(this.$route.query);
        }
    }
}
</script>

<style lang="less">
    .searchContainer{
        width: 100%;
        .searchNav{
            width: 100%;
            height: 100px;/*px*/
            box-sizing: border-box;
            border-bottom: 1px solid #ccc;/*px*/
            display: flex;
            justify-content: space-between;
            padding:20px;/*px*/
            .icon-xiangzuojiantou{
                width: 10%;
            }
            .searchBar {
                width: 80%;
                height: 60px;/*px*/
                background:#ECECEC;
                .iconfont{
                    font-size: 30px;/*px*/
                }
            }
            .iconfont{
                width: 10%;
                height: 60px;/*px*/
                text-align: center;
                font-size: 50px;/*px*/
                line-height: 60px;/*px*/
                color: #8F8F8F;
            }
        }
        .allList{
            width: 100%;
            height: 80px;/*px*/
            border-bottom: 1px solid #ccc;/*px*/
            display: flex;
            justify-content: space-between;
            .searchList{
                width: 25%;
                height:30px;/*px*/
                line-height: 30px;/*px*/
                text-align: center;
                font-size: 30px;/*px*/
                margin-top: 25px;/*px*/
                border-left: 1px solid #ccc;/*px*/
            }
        }
        .searchTypeContainer{
            width: 100%;
            position: absolute;
            top: 180px;/*px*/
            bottom: 0px;/*px*/
            left: 0px;/*px*/
            right: 0px;/*px*/
            .wrapper{
                width: 100%;
                height: 100%;
            }
        }
        .listSearch{
            color:#F67592;
        }
    }
</style>



