<template>
    <div class="allType">
        <div v-for="(v,i) of rightType" :key="'rightType'+i" class="type" @click="goToDetail(v)">
            <div class="typeImg"><img :src="v.img"></div>
            <div class="typeTitle">{{ v.title }}</div>
            <div class="typeDesc">
                <span class="typePrice">¥{{ v.price }}</span>
                <span class="typeCfav">{{ v.cfav }}<i class="iconfont icon-shoucang"></i></span>
            </div>
        </div>
        <div v-for="(v,i) of (2-(rightType.length%2==0?2:rightType.length%2))" :key="'emptyType'+i" class="emptyType"></div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
    props:{
        rightType:null
    },
    computed:{
        ...mapState(['home'])
    },
    methods:{
        ...mapMutations(['setChangeData']),
        goToDetail(v){       
            this.setChangeData(v);
            this.$router.push('/detail');
        }
    }
}
</script>

<style lang="less">
    .allType{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        .type{
            width: 48%;
            height: 450px;/*px*/
            margin-top: 10px;/*px*/
            .typeImg{
                width: 100%;
                height: 350px;/*px*/
                text-align: center;
                border-radius: 10px 10px 0px 0px;/*px*/
                img{
                    width: 100%;
                    height: 350px;/*px*/
                }
            }
            .typeTitle{
                margin-top: 10px;/*px*/
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
            .typeDesc{
                margin-top: 10px;/*px*/
                display: flex;
                justify-content: space-between;
            }
            .typePrice{
                font-size: 30px;/*px*/
                font-weight: bold;
                color:#F67592;
            }
            .typeCfav{
                color: #AEB9BE;
                .icon-shoucang{
                    margin-left: 10px;/*px*/
                }
            }
        }
    }
</style>



