<template>
    <div class="leftClassify">
        <div :class="v.maitKey | isClick(key)" v-for="(v,i) of leftClassify" :key="'leftClassify'+i" class="bigTitle"  @click="leftChange(v)">
            <div  class="leftTitle">{{ v.title }}</div> 
        </div>
    </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
    data() {
        return {
            key: "41888"
        }
    },
    filters:{
        isClick(value,key){
            return value==key?"smallTitle":"";
        }
    },
    props:{
        leftClassify:null
    },
    methods:{
        ...mapActions(['loadClassify','loadSearch']),
        leftChange(p){
            // console.log(p);
            this.key=p.maitKey;
            this.loadClassify(p.maitKey);
            this.loadSearch({fcid:p.miniWallkey,sort:'pop'});
        }
    }
}
</script>

<style lang="less">
    .leftClassify{
        width: 100%;
        font-size: 30px;/*px*/
        text-align: center;
        .bigTitle{
            width: 100%;
            height: 100px;/*px*/
            .leftTitle{
                width: 100%;
                height: 40px;/*px*/
                line-height: 40px;/*px*/
                margin-top: 30px;/*px*/
            }
        }
        .smallTitle{
            background: #FFFFFF;
            .leftTitle{
                color: #F67592;
                border-left: 10px solid #F67592;
                box-sizing: border-box;
            }    
        }
    }
</style>



