<template>
    <div class="body">
        <div class="wrapper" ref="home">
            <div class="content">
                <div>
                    <!-- 轮播图 -->
                    <Swiper :warpImgs="home.warpImgs"></Swiper>
                </div>
                <div>
                    <!-- 分类导航 -->
                    <LitterNav :types="home.types"></LitterNav>
                </div>
                <div>
                    <div class="title">超实惠-促销直达</div>
                    <!-- 促销直达 -->
                    <Bebefit :bebefit="home.bebefit"></Bebefit>
                </div>
                <div>
                    <div class="title">热门分类</div>
                    <!-- 热门分类 -->
                    <HotType :hotType="home.hotType"></HotType>
                </div>
                <div>
                    <div class="title">猜你喜欢</div>
                    <!-- 猜你喜欢 -->
                    <Likes :likes="home.likes"></Likes>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import BScroll from "better-scroll";
    import { mapState, mapActions} from 'vuex';
    
    import Swiper from './home/Swiper';
    import LitterNav from './home/LitterNav';
    import Bebefit from './home/Bebefit';
    import HotType from './home/HotType';
    import Likes from './home/Likes';

    export default {
        components: {
            Swiper,LitterNav,Bebefit,HotType,Likes
        },
        computed: {
            ...mapState(['home'])
        },
        methods: {
            initScroll() {
                this.$nextTick(() => {
                    this.scroll = new BScroll(this.$refs.home, {
                        click:true
                    });
                });
            },
            ...mapActions(['loadHomeData','loadLikes'])
        },
        mounted(){
            this.initScroll();
            this.loadHomeData();
            this.loadLikes();
        }
    }
</script>

<style lang="less">
    .body {
        position: absolute;
        top: 100px; /*px*/
        bottom: 100px; /*px*/
        left: 0px;
        right: 0px;
    }
    .wrapper{
        width: 100%;
        height: 100%;
    }
    .title{
        width: 100%;
        font-size: 25px;/*px*/
        text-align: center;
        background: url(../../static/img/1.png) no-repeat;
        background-position: center;
        background-size: 60%;
        margin-top: 50px;/*px*/
    }
</style>





