import Vue from 'vue'
import Router from 'vue-router'

import BodyComponent                     from '../components/BodyComponent';
import HomeComponent                     from '../components/HomeComponent';
import ClassifyComponent                 from '../components/ClassifyComponent';
import SearchComponent                   from '../components/SearchCopmponent';
import SearchNav                         from '../components/SearchNav';
import DetailComponent                   from '../components/DetailComponent';

Vue.use(Router);

export default new Router({
    mode:"history",
    linkActiveClass:"active-class",
    linkExactActiveClass:"exact-active-class",
    routes: [
        {path: '/',redirect:"/body"},
        {path:"/body",component:BodyComponent,children:[
            {path:"/",redirect:"home"},
            {path:"home",component:HomeComponent},
            {path:"classify",component:ClassifyComponent}
        ]},
        {path:"/search",component:SearchComponent},
        {path:"/nav",component:SearchNav},
        {path:"/detail",component:DetailComponent}
    ]
})



