<template>
    <div class="detailContainer">
        <div class="wrapper" ref="detail">
            <div class="content">
                <div class="detailBody">
                    <div class="detailImg"><img :src="img"></div>
                    <div class="detailTitle">{{ title }}</div>
                    <div class="detailPrice">
                        <div class="newPrice">¥<span>{{ price }}</span></div>
                        <div class="oldPrice">
                            <div class="price">¥{{ orgPrice }}</div>
                            <div class="desc">拼团价</div>
                        </div>
                    </div>  
                </div>
                <div v-for="(v,i) of home.review" :key="'review'+i" class="review">
                    <div class="reviewImg"><img :src="v.user.avatar"><span>{{ v.user.uname }}</span></div>
                    <div class="reviewContent">{{ v.content }}</div>
                    <div class="reviewTime">{{ v.time }}</div>
                </div>
            </div>
        </div>    
    </div>
</template>

<script>
import BScroll from "better-scroll";
import { mapState, mapActions} from 'vuex';

export default {
    data(){
        return{
            img:"",
            title:"",
            price:"",
            orgPrice:""
        }
    },
    computed: {
        ...mapState(['home'])
    },
    methods: {
        ...mapActions(['loadReview']),
        initScroll() {
            this.$nextTick(() => {
                this.scroll = new BScroll(this.$refs.detail, {
                    click:true
                });
            });
        },
        change(){
            // console.log(this.$store);
            let a=this.$store.state.home.changeData;
            this.img=a.img;
            this.title=a.title;
            this.price=a.price;
            this.orgPrice=a.orgPrice;
        }
    },
    mounted() {
        this.loadReview(this.home.changeData.tradeItemId);
        this.change();
        this.initScroll();
    }
}
</script>

<style lang="less">
    .detailContainer{
        width: 100%;
        height: 100%;
        .wrapper{
            width: 100%;
            height: 100%;
            .content{
                padding-bottom: 50px;/*px*/
            }
        }
        .detailBody{
            width: 90%;
            margin: 0 auto;
            border-bottom: 1px solid #ccc;
            .detailImg{
                width: 100%;
                img{  
                    width: 100%;
                }
            }
            .detailTitle{
                font-size: 30px;/*px*/
                margin-top: 20px;/*px*/
            }
            .detailPrice{
                .newPrice{
                    display: inline-block;
                    span{
                        font-size: 60px;/*px*/
                    }
                }
                .oldPrice{
                    display: inline-block;
                    .price{
                        color: #ccc;
                        text-decoration: line-through;
                    }
                    .desc{
                        color: #F67592;
                        background: pink;
                    }
                }
            }
        }
        .review{
            width: 100%;
            padding: 30px;/*px*/
            box-sizing: border-box;
            .reviewImg{
                width: 50%;
                img{
                    width: 20%;
                }
                span{
                    font-size: 30px;/*px*/
                    margin-left: 20px;/*px*/
                }
            }
            .reviewContent{
                font-size: 30px;/*px*/
                margin-top: 20px;/*px*/
            }
            .reviewTime{
                color: #ccc;
                margin-top: 10px;/*px*/
            }
        }
    }
</style>



