<template>
    <div class="classify">
        <!-- 左侧 -->
        <div class="left-container">
            <div class="wrapper" ref="classifyLeft">
                <div class="content">                   
                    <LeftComponent :leftClassify="home.leftClassify"></LeftComponent>                   
                </div>
            </div>
        </div>
        <!-- 右侧 -->
        <div class="right-container">
            <div class="wrapper" ref="classifyRight">
                <div class="content">                   
                    <RightComponent :rightClassify="home.rightClassify"></RightComponent>
                    <!-- 右侧列表 -->
                    <div class="area">                    
                        <div :class="v.sortKey | isClick(key)" v-for="(v,i) of home.search" :key="'search'+i" @click="areaChange(v)">{{ v.title }}</div>
                    </div>
                    <!-- 右侧排序 -->
                    <RightType :rightType="home.rightType"></RightType>            
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BScroll from "better-scroll";
import { mapState, mapActions} from 'vuex';

import LeftComponent from './classify/LeftComponent';
import RightComponent from './classify/RightComponent';
import RightType from './classify/RightType';

export default {
    components: {
        LeftComponent,RightComponent,RightType
    },
    data() {
        return {
            key: "pop"
        }
    },
    filters:{
        isClick(value,key){
            return value==key?"areaTitle":"";
        }
    },
    computed: {
        ...mapState(['home'])
    },
    methods: {
        initScroll() {
            this.$nextTick(() => {
                this.scroll = new BScroll(this.$refs.classifyLeft, {
                    click:true
                });
                this.scroll = new BScroll(this.$refs.classifyRight, {
                    click:true
                });
            });
        },
        areaChange(p){
            // console.log(p);
            this.key=p.sortKey;
            this.loadSearch(p.sortKey);
        },
        ...mapActions(['loadHomeData','loadClassify','loadSearch'])
    },
    mounted(){
        this.initScroll();
        this.loadHomeData();
        this.loadClassify();
        this.loadSearch();
    }
}
</script>

<style lang="less">
    .classify{
        width: 100%;
        display: flex;
    }
    .wrapper{
        width: 100%;
        height: 100%;
    }
    .left-container{
        width: 25%;
        background:  #F6F6F6;
        position: absolute;
        top: 100px;/*px*/
        bottom: 100px;/*px*/
        left: 0px;/*px*/
        right: 0px;/*px*/
    }
    .right-container{
        width: 75%;
        position: absolute;
        top: 100px;/*px*/
        bottom: 100px;/*px*/
        left: 25%;
        right: 0px;/*px*/
    }
    .area{
        width: 100%;
        height: 80px;/*px*/
        border-bottom: 1px solid #ccc;
        margin: 0 auto;
        display: flex;
        div{
            width:100%/3;
            height: 30px;/*px*/
            text-align: center;
            line-height: 30px;/*px*/
            font-size: 30px;/*px*/
            margin-top: 25px;/*px*/
            border-left: 1px solid #ccc;
        }
        .areaTitle{
            color:#F67592;
        }
    }
</style>




